import React, { Suspense } from "react"
import Layout from "./containers/Layout/Layout"
import SeedChecker from "./containers/SeedChecker/SeedChecker"
import SeedRecover from "./containers/SeedRecover/SeedRecover"
import Tutorial from "./containers/Tutorial/Tutorial"
import Writeup from "./containers/Writeup/Writeup"
import { HashRouter, Route, Routes } from "react-router-dom"

function App() {
    return (
        <Suspense fallback="loading">
            <HashRouter basename="/">
                <Layout>
                    <Routes>
                        <Route path="/" element={<SeedChecker />} />
                        <Route path="/seedrecover" element={<SeedRecover />} />
                        <Route path="/tutorial" element={<Tutorial />} />
                        <Route path="/writeup" element={<Writeup />} />
                    </Routes>
                </Layout>
            </HashRouter>
        </Suspense>
    )
}

export default App
