import React from "react"
import classes from "./GearSlot.module.css"
import Select from "react-select"

const GearSlot = props => {
    const abilities = [
        { value: "None", label: "None" },
        { value: "InkRecovery_Up", label: "Ink Recovery Up" },
        { value: "OpInkEffect_Reduction", label: "Ink Resistance Up" },
        { value: "MainInk_Save", label: "Ink Saver (Main)" },
        { value: "SubInk_Save", label: "Ink Saver (Sub)" },
        { value: "Action_Up", label: "Intensify Action" },
        { value: "RespawnTime_Save", label: "Quick Respawn" },
        { value: "JumpTime_Save", label: "Quick Super Jump" },
        { value: "HumanMove_Up", label: "Run Speed Up" },
        { value: "SpecialIncrease_Up", label: "Special Charge Up" },
        { value: "SpecialSpec_Up", label: "Special Power Up" },
        { value: "RespawnSpecialGauge_Save", label: "Special Saver" },
        { value: "SubSpec_Up", label: "Sub Power Up" },
        { value: "SubEffect_Reduction", label: "Sub Resistance Up" },
        { value: "SquidMove_Up", label: "Swim Speed Up" },
    ]

    const drinks = [
        { value: "None", label: "None" },
        { value: "Action_Up", label: "Actionberry Twist" },
        { value: "SquidMove_Up", label: "Cherry Apple Fizz" },
        { value: "JumpTime_Save", label: "Citrus Hip-Hopper" },
        { value: "InkRecovery_Up", label: "Ink-Well Chiller" },
        { value: "SpecialIncrease_Up", label: "Ma's Special Blend" },
        { value: "MainInk_Save", label: "Main Mega Pucker-Up" },
        { value: "RespawnTime_Save", label: "Matcha Reviver" },
        { value: "RespawnSpecialGauge_Save", label: "Salty Melon Special" },
        { value: "SpecialSpec_Up", label: "Smoothie Especial" },
        { value: "HumanMove_Up", label: "Speedy Gazpacho" },
        { value: "OpInkEffect_Reduction", label: "Stompin' Grape" },
        { value: "SubInk_Save", label: "Sub Berry Blaster Blitz" },
        { value: "SubEffect_Reduction", label: "Sub Lime" },
        { value: "SubSpec_Up", label: "Super Sub Juicer" },
    ]

    const centerStyle = {
        singleValue: (styles, { data }) => ({ ...styles, display: "flex", alignItems: "center" }),
    }

    return (
        <div className={classes.SettingsContent}>
            <p>
                <strong>Slot {props.idx}:</strong>
            </p>
            <Select
                value={props.currentSlot}
                onChange={e => props.changeSlot(e, props.idx)}
                options={abilities}
                styles={centerStyle}
                defaultValue={abilities[0]}
                formatOptionLabel={elem => {
                    const url = `https://leanny.github.io/splat3/images/skill/${elem.value}.png`
                    return (
                        <>
                            {elem.value !== "None" ? <img src={url} width="32" alt={elem.label} /> : ""}
                            <span> {elem.label}</span>
                        </>
                    )
                }}
            />
            <p>
                <strong>Drink {props.idx}:</strong>
            </p>
            <Select
                value={props.currentDrink}
                onChange={e => props.changeDrink(e, props.idx)}
                options={drinks}
                defaultValue={drinks[0]}
                styles={centerStyle}
                formatOptionLabel={elem => {
                    const url = `https://leanny.github.io/splat3/images/skill/${elem.value}.png`
                    return (
                        <>
                            {elem.value !== "None" ? <img src={url} width="32" alt={elem.label} /> : ""}
                            <span> {elem.label}</span>
                        </>
                    )
                }}
            />
        </div>
    )
}

export default GearSlot
