import classes from "./Settings.module.css"
import Select from "react-select"

const Settings = props => {
    const brands = [
        { value: "B99", label: "amiibo" },
        { value: "B15", label: "Annaki" },
        { value: "B19", label: "Barazushi" },
        { value: "B98", label: "Cuttlegear" },
        { value: "B20", label: "Emberz" },
        { value: "B16", label: "Enperry" },
        { value: "B06", label: "Firefin" },
        { value: "B05", label: "Forge" },
        { value: "B97", label: "Grizzco" },
        { value: "B09", label: "Inkline" },
        { value: "B02", label: "Krak-On" },
        { value: "B03", label: "Rockenberg" },
        { value: "B07", label: "Skalop" },
        { value: "B08", label: "Splash Mob" },
        { value: "B00", label: "SquidForce" },
        { value: "B11", label: "Takoroka" },
        { value: "B10", label: "Tentatek" },
        { value: "B17", label: "Toni Kensa" },
        { value: "B04", label: "Zekko" },
        { value: "B01", label: "Zink" },
    ]

    const abilities = [
        { value: "None", label: "None" },
        { value: "Action_Up", label: "Actionberry Twist" },
        { value: "SquidMove_Up", label: "Cherry Apple Fizz" },
        { value: "JumpTime_Save", label: "Citrus Hip-Hopper" },
        { value: "InkRecovery_Up", label: "Ink-Well Chiller" },
        { value: "SpecialIncrease_Up", label: "Ma's Special Blend" },
        { value: "MainInk_Save", label: "Main Mega Pucker-Up" },
        { value: "RespawnTime_Save", label: "Matcha Reviver" },
        { value: "RespawnSpecialGauge_Save", label: "Salty Melon Special" },
        { value: "SpecialSpec_Up", label: "Smoothie Especial" },
        { value: "HumanMove_Up", label: "Speedy Gazpacho" },
        { value: "OpInkEffect_Reduction", label: "Stompin' Grape" },
        { value: "SubInk_Save", label: "Sub Berry Blaster Blitz" },
        { value: "SubEffect_Reduction", label: "Sub Lime" },
        { value: "SubSpec_Up", label: "Super Sub Juicer" },
    ]

    const centerStyle = {
        singleValue: (styles, { data }) => ({ ...styles, display: "flex", alignItems: "center" }),
    }

    return (
        <div className={classes.SettingsContent}>
            <p>
                Please select the Brand of the gear. Also, please select the Drink Ticket you want to activate (if any).
            </p>
            <p>
                <strong>Brand:</strong>
            </p>
            <Select
                value={props.currentBrand}
                onChange={props.brandChange}
                options={brands}
                styles={centerStyle}
                formatOptionLabel={elem => {
                    const url = `https://leanny.github.io/splat3/images/brand/${elem.value}.png`
                    return (
                        <>
                            <img src={url} width="32" alt={elem.label} />
                            <span> {elem.label}</span>
                        </>
                    )
                }}
            />
            <p>
                <strong>Drink:</strong>
            </p>
            <Select
                value={props.currentDrink}
                onChange={props.drinkChange}
                options={abilities}
                styles={centerStyle}
                formatOptionLabel={elem => {
                    const url = `https://leanny.github.io/splat3/images/skill/${elem.value}.png`
                    return (
                        <>
                            {elem.value !== "None" ? <img src={url} width="32" alt={elem.label} /> : ""}
                            <span> {elem.label}</span>
                        </>
                    )
                }}
            />
        </div>
    )
}

export default Settings
