import classes from "./Tutorial.module.css"
import image01 from "../../assets/images/unlock_gear_01.png"
import image02 from "../../assets/images/unlock_gear_02.png"
import image03 from "../../assets/images/unlock_gear_03.png"
import image04 from "../../assets/images/unlock_gear_04.png"
import image05 from "../../assets/images/unlock_gear_05.png"
import tool_01 from "../../assets/images/tool_01.png"
import tool_02 from "../../assets/images/tool_02.png"
import tool_03 from "../../assets/images/tool_03.png"
import tool_04 from "../../assets/images/tool_04.png"
import tool_05 from "../../assets/images/tool_05.png"
import tool_06 from "../../assets/images/tool_06.png"

const Tutorial = props => {
    return (
        <>
            <h1>Splatoon 3 - Gear Seed Checker Tutorial</h1>
            <div className={classes.Content}>
                <strong>
                    Note: If you make videos about this tutorial, feel free to link them to me, so I can link them here.
                </strong>
            </div>
            <div className={classes.Content}>
                <h2>About</h2>
                <p>
                    In Splatoon 3 every piece of gear has a seed. This seed determines every future roll of your gear,
                    as well as if drink tickets take effect or not.
                </p>
                <h2>Getting the Seed</h2>
                <p>The seed can be obtained in two ways:</p>
                <ul>
                    <li>Reading it from the savegame</li>
                    <li>Getting enough rolls and calculate the seed</li>
                </ul>
                <h3>From Savegame</h3>
                <p>
                    This requires a hackable switch. Extract your savegame, decrypt it, and then look for your gear. The{" "}
                    <code>RandomContext</code> of the gear is the seed. There will be no further instructions about this
                    topic here.
                </p>
                <h3>From Rolls</h3>
                <p>
                    This method does not require a hackable switch. In order to use this method, you need to level your
                    gear and note down every roll you get. Here are a few examples of what a roll looks like:
                </p>
                <h4>Level Up from Empty Slot</h4>
                <p>
                    When you level your gear and unlock an empty slot, the new ability can be entered in the calculator.
                    In the example here you notice that "Quick Super Jump" got unlocked.
                </p>
                <img src={image01} alt="unlock_gear_01" /> <img src={image02} alt="unlock_gear_02" />
                <p>
                    Note: Using a drink requires you to set the drink as well. When you unlock an ability under the
                    influence of a drink, then this symbol can be seen.
                </p>
                <img src={image03} alt="unlock_gear_03" width="256" />
                <h4>Level Up from Full Slot</h4>
                <p>Here you enter the ability that pops up at the end.</p>
                <img src={image05} alt="unlock_gear_05" width="256" />
                <h4>From Rerolls</h4>
                <p>
                    Rerolling gives you 3 abilities at once. Enter them with NO Drink effect and from left to right in
                    the calculator.
                </p>
                <img src={image04} alt="unlock_gear_04" width="256" />
                <h4>Example</h4>
                <p>
                    For this example I use SquidForce branded gear. After leveling all three slots, I get "Ink Recovery
                    Up", "Quick Respawn", and "Ink Recovery Up". I enter these in Slot 0, 1, and 2. When you click on{" "}
                    <code>Search Seed</code> then it will give you a list of many seeds quite fast.
                </p>
                <img src={tool_01} alt="tool_01" />
                <p>
                    In order to reduce the number of possible seeds, we need to add more rolls. Here I scrub my gear and
                    unlock the next three slots. I get three times "Ink Resistance Up". I put it all in the tool again.
                </p>
                <img src={tool_02} alt="tool_02" />
                <p>
                    Here, I still get too many possible seeds, so I continue to level it. On my next three rolls I get
                    "Special Power Up", "Quick Super Jump", "Run Speed Up". With nine slots filled, we can reduce the
                    number of possible seeds to 4.
                </p>
                <img src={tool_03} alt="tool_03" />
                <p>For the next slot I unlock "Swim Speed Up" and search again.</p>
                <img src={tool_04} alt="tool_04" />
                <p>
                    Now I end up with the seed <code>0x83612e4f</code> for this specific piece of gear and can now
                    predict all future rolls on this specific piece of gear.
                </p>
                <h2>Calculating future results</h2>
                <p>
                    Here, simply enter your brand, if you want to use a Drink Ticket or not, enter your seed, and then
                    click on <code>Display Results</code>. This will show you your future rolls for this specific
                    configuration.
                </p>
                <img src={tool_05} alt="tool_05" />
                <p>The interface in more detail:</p>
                <ol>
                    <li>The brand of your gear</li>
                    <li>
                        The drink you have active during level up. Note that snail rerolling is NOT affected by drinks
                    </li>
                    <li>The current seed of your gear</li>
                    <li>The slot you unlock next, starting with slot 0</li>
                    <li>The ability you will roll on this slot</li>
                    <li>The seed of your gear when you reach this roll</li>
                </ol>
                <p>Here is the same seed, but with a drink ticket active:</p>
                <img src={tool_06} alt="tool_06" />
                <p>
                    It is advices to keep track of of the new seed for your gear for every level when you use a Drink
                    Ticket.
                </p>
            </div>
        </>
    )
}

export default Tutorial
