import React from "react"
import Input from "../../components/UI/Input/Input"
import classes from "./Controls.module.css"

const Controls = props => {
    return (
        <div className={classes.Wrapper}>
            <Input title="Current Seed" changed={props.seedValidator} val={props.currentSeed} />
            <Input title="Start Frame" val={props.currentStartFrame} changed={props.startValidator} />
            <Input title="Number of Frames" val={props.currentNumberOfFrames} changed={props.numberValidator} />
            <div>
                <button className={classes.Button} onClick={props.calculateResults}>
                    Display Results
                </button>
            </div>
        </div>
    )
}

export default React.memo(Controls)
